import * as React from 'react';
import {StyleSheet, Text, View, useWindowDimensions, Dimensions, TouchableOpacity, Image} from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import LinearGrad from "../LinearGrad";
import Svg, { Circle, Rect } from 'react-native-svg';

export default function HeaderStart({navigation}) {
    const windowWidth = useWindowDimensions().width;
    const windowHeight = useWindowDimensions().height;
    return (
        <View style={[styles.container,{width:windowWidth}]}>
            <View style={[styles.content,{height: windowHeight/1.5}]}>
                <LinearGrad/>
                    <Image
                        style={styles.logo}
                        source={{
                            uri:
                               "data:image/png;base64,"
                        }}
                    />
                <Text style={styles.title}>Boddy Body</Text>
                <Text style={styles.title}>Your motivational training app</Text>
            </View>
        </View>
    );
}

const styles = StyleSheet.create({
    container : {
        backgroundColor: 'white',
    },
    content : {
        height: 500,
        padding: 20,
        justifyContent: 'center',
        alignItems: 'center',
        borderBottomLeftRadius: 15,
        borderBottomRightRadius: 15,
        overflow: 'hidden',
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 6,
        },
        shadowOpacity: 0.16,
        shadowRadius: 10.00,
        elevation: 1,
    },
    title : {
        fontSize: 24,
        color: 'white',
        fontWeight: 600,
        textAlign: 'center',
        marginTop: 10
    },
    logo: {
        width: 150,
        height: 150,
    },
});